import React, {useEffect, useState} from 'react';
import styled from 'styled-components';
import MainContext from '../../context/MainContext';
import Visit from './Visit';

const FieldStyles = styled.div`
    border-bottom: 1px solid var(--grey);
    border-right: 1px solid var(--grey);
    height: 50px;
`

export default function Field({timeStart, timeEnd, workerName, color}) {

    const [offTop, setOffTop] = useState(0);
    const [offLeft, setOffLeft] = useState(0);
    const [height, setHeight] = useState(0);

    const myRef = React.createRef();
    const context = React.useContext(MainContext);

    const { visits } = context;

    useEffect(() => {
        setOffTop(myRef.current.offsetTop);
        setOffLeft(myRef.current.offsetLeft);
        setHeight(myRef.current.clientHeight);
    }, [myRef])

    return (
        <FieldStyles
            ref={myRef}
            timeStart={timeStart}
            timeEnd={timeEnd}
            workerName={workerName}
        >   
            {visits.map(visit => {
                if (visit.worker === workerName && timeStart === visit.start) {
                    return (
                        <Visit
                        key={visit.date}
                        visit={visit}
                        top={offTop}
                        color={color}
                        height={height}
                    />
                    )
                }
                })}
        </FieldStyles>
    )
}